import React, { useState}  from 'react';
import { useGlobal } from 'reactn'
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle'

const styles = theme => ({
    avatar: {
      margin: 10,
    },  
});
function Login(props) {
    // const [profileImageUrl, setprofileImageUrl] = useState('')
    // Set an initial global state directly:
    const [ profileImageUrl, setProfileImageUrl ] = useGlobal('profileImageUrl')
    window.gapi.load('client:auth2', function() {
        window.gapi.auth2.init({client_id: '1001750562670-13tl5gmis7taghhs2608c3rkkfs0ug46.apps.googleusercontent.com'});
    });
    function authenticate() {
        return window.gapi.auth2.getAuthInstance()
            .signIn({scope: 'https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.appdata https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/drive.metadata https://www.googleapis.com/auth/drive.metadata.readonly https://www.googleapis.com/auth/drive.photos.readonly https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/spreadsheets'})
            .then(function(response) { 
                console.log(response)
                
                sessionStorage.setItem('WE', response)  
                if (response.w3.Paa) {
                    setProfileImageUrl(response.w3.Paa)
                    //setGlobal('WE', response)
                    // setCards(response.w3.Paa)
                    
                }
                else
                    setProfileImageUrl(AccountCircle)
                console.log('Sign-in successful'); 
            }, function(err) { console.error('Error signing in', err); });
    }
    function loadClient() {
        return window.gapi.client.load('https://content.googleapis.com/discovery/v1/apis/drive/v3/rest')
            .then(function() {
                console.log('GAPI client loaded for API'); 
                execute()    
            },
                  function(err) { console.error('Error loading GAPI client for API', err); });
    }
    function execute() {
        return window.gapi.client.drive.files.list({})
            .then(function(response) {
                    // Handle the results here (response.result has the parsed body).
                    console.log('Response', response);
                  },
                  function(err) { console.error('Execute error', err); });
    }   
    const logout = () => {
        // sessionStorage.removeItem('avatar')    
        setProfileImageUrl('')
        
      }
    let Profile = () => {}; 
    console.log(profileImageUrl)
    if (profileImageUrl &&
        profileImageUrl.length > 0) {
        Profile = () => {
            return (
                <IconButton onClick={() => {logout()}}>
                    <Avatar alt='Remy Sharp' src={profileImageUrl} className={styles.avatar} />
                </IconButton>
            )
        }
    }
    else {     
        Profile = () => {
            return (
                <div>       
                    <Button onClick={() => {authenticate().then(loadClient)}} color='inherit'>login</Button>                    
                </div>
            )
        }
    }
    return (                   
        <Profile />    
    )
    
}
//On load, called to load the auth2 library and API client library.
export default Login