import React, { useState } from 'react'
import { useGlobal } from 'reactn'
import { withStyles } from '@material-ui/core/styles'
import { withRouter } from 'react-router'
import useSheet from '../../hooks/useSheet'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'

const styles = theme => ({  
    root: {
      flexGrow: 1,
    },
    appBar: {
      position: 'relative',
    },   
    title: {
        marginLeft: theme.spacing.unit * 2,
        flexGrow: 1,
    }
})
function Transition(props) {
    return <Slide direction='up' {...props} />
}
function M8trixAdd(props) {    
    const { classes } = props
    const [ m8trixAddOpen = false, setM8trixAddOpen ] = useGlobal('m8trixAddOpen')
    const [ gifs, setGifs ] = useGlobal('gifs')
    const [ content, setContent ] = useState()
    
    window.gapi.load("client:auth2", function() {
      window.gapi.auth2.init({client_id: "1001750562670-13tl5gmis7taghhs2608c3rkkfs0ug46.apps.googleusercontent.com"});
    });

    function handleChangeTextField(event) {
      setContent(event.target.value)
    }
    function handleScrapeAlbum() {    
        let gifs = content.split(',')
                          .filter(gif => gif.startsWith('["https://lh3'))
                          .map(gif => gif.replace(/(\[\"|\")/g, '').replace(/(\r\n\t|\n|\r\t)/gm,""))
        if (gifs) {
            setGifs(gifs)  
            props.history.push('/m8trix')
        }      
    }
    
    return (
      <div>      
        <Dialog
            fullScreen
            open={m8trixAddOpen}
            onClose={() => {setM8trixAddOpen(false)}}
            TransitionComponent={Transition}>
            <AppBar className={classes.appBar}>
              <Toolbar>
                <IconButton color='inherit' onClick={() => {setM8trixAddOpen(false)}} aria-label='Close'>
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' color='inherit' className={classes.title}>
                  M8trix Add
                </Typography>
                <Button color='inherit' onClick={() => {handleScrapeAlbum()}}>
                    <Typography variant='h6' color='inherit' >
                    Scrape
                    </Typography>
                </Button>
              </Toolbar>
            </AppBar>
            <TextField
              id='outlined-multiline-flexible'
              label='M8trix Add'
              multiline
              rows='33'                  
              value={content}      
              onChange={handleChangeTextField}
              className={classes.textField}
              margin='normal'
              variant='outlined'/>
          </Dialog>
      </div>
    )
}
export default withRouter(withStyles(styles)(M8trixAdd))