import React, { useState } from 'react'
import { useGlobal } from 'reactn'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'
import Icon from '@material-ui/core/Icon'
import NavigationIcon from '@material-ui/icons/Navigation'
import ReactSVG from 'react-svg'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import FileCopyIcon from '@material-ui/icons/FileCopyOutlined'
import SaveIcon from '@material-ui/icons/Save'
import PrintIcon from '@material-ui/icons/Print'
import ShareIcon from '@material-ui/icons/Share'
import DeleteIcon from '@material-ui/icons/Delete'
import matrix from '../../assets/images/matrix.svg'


const styles = theme => ({
  fab: {
    margin: theme.spacing.unit,
    position: 'absolute',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 3,        
  },
  extendedIcon: {
    marginRight: theme.spacing.unit,
  },
  speedDial: {
    position: 'absolute',    
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 3,
    
  },
})

function Fly(props) {
  const { classes } = props  
  const [ m8trixAddOpen, setM8trixAddOpen ] = useGlobal('m8trixAddOpen', false)
  const [ speedDialOpen, setSpeedDialOpen ] = useState(false)



  const actions = [
    { icon: <FileCopyIcon />, name: 'Copy' },
    { icon: <SaveIcon />, name: 'Save' },
    { icon: <PrintIcon />, name: 'Print' },
    { icon: <ShareIcon />, name: 'Share' },
    { icon: <ReactSVG src={matrix} />, name: 'M8trix' },
  ];

  
  function handleClick () {
    setSpeedDialOpen(!speedDialOpen)
    // this.setState(state => ({
    //   open: !state.open,
    // }));
  };

  function handleClose () {
    setSpeedDialOpen(false)
    // this.setState({ open: false });
  };

  function handleOpen () {
    setSpeedDialOpen(true)
    // this.setState({ open: true });
  };

  return (
    <div>
      <SpeedDial
        ariaLabel='SpeedDial example'
        className={classes.speedDial}
        icon={<SpeedDialIcon />}        
        onClick={handleClick}
        onClose={handleClose}
        // onFocus={handleOpen}
        // onMouseEnter={handleOpen}
        // onMouseLeave={handleClose}
        open={speedDialOpen}
        direction='up'
      >
      {actions.map(action => (
        <SpeedDialAction
          key={action.name}
          icon={action.icon}
          tooltipTitle={action.name}
          onClick={action.name === 'M8trix' ? () => {setM8trixAddOpen(true)} : handleClick}
        />
      ))}

      </SpeedDial>
      {/* <Fab color='primary' aria-label='Add' className={classes.fab} onClick={() => {handleCardAddOpen()}}>
        <AddIcon />
      </Fab>       */}
    </div>
  )
}

Fly.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Fly)