
import React, { useState } from 'react'
import { useGlobal } from 'reactn'
import { withStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import Drawer from '@material-ui/core/Drawer'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'

const styles = theme => ({  
  root: {
    flexGrow: 1,
  },
  appBar: {
    position: 'relative',
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    
    justifyContent: 'flex-end',
  },
  title: {
    flexGrow: 1,
  },
  // textField: {
  //   paddingLeft: theme.spacing.unit * 1,
  //   paddingRight: theme.spacing.unit * 1,
  // }
})


function Transition(props) {
  return <Slide direction='up' {...props} />
}
function Scrape(props) {
  
  const { classes } = props;
  const [ scraperOpen, setScraperOpen ] = useGlobal('scraperOpen')
  const [ gifs, setGifs ] = useGlobal('gifs')
  const [ content, setContent ] = useState()
  function handleChangeTextField(event) {
    setContent(event.target.value)
  }
  function handleScrapeAlbum() {    
    let gifs = this.state.content
                    .split(',')
                    .filter(gif => gif.startsWith('["https://lh3'))
                    .map(gif => gif.replace(/(\[\"|\")/g, '').replace(/(\r\n\t|\n|\r\t)/gm,""))
    if (gifs) {
      setGifs(gifs)        
    }      
  }
  return (
    <div>      
      <Dialog
          fullScreen
          open={scraperOpen}
          onClose={() => {setScraperOpen(false)}}
          TransitionComponent={Transition}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton color='inherit' onClick={() => {setScraperOpen(false)}} aria-label='Close'>
                <CloseIcon />
              </IconButton>
              <Typography variant='h6' color='inherit' className={classes.title}>
                Scrape
              </Typography>
              <Button color='inherit' onClick={() => {handleScrapeAlbum()}}>
              <Typography variant='h6' color='inherit' >
                Do it
              </Typography>
              </Button>
            </Toolbar>
          </AppBar>
          <TextField
            id='outlined-multiline-flexible'
            label='Scrape'
            multiline
            rows='33'    
            
            value={content}      
            onChange={handleChangeTextField}
            className={classes.textField}
            margin='normal'
            variant='outlined'/>
        </Dialog>
    </div>
  )
}
export default withStyles(styles)(Scrape)